body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
  font-family: "Poppins", sans-serif;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
  font-family: "Poppins", sans-serif;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

* {
  scroll-behavior: smooth;
}

body {
  min-height: 1000px;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 0 100px;
  background-color: #000;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  transition: 1s;
  z-index: 3;
}

@media (max-width: 992px) {
  header {
    padding: 40px;
  }
}
header.sticky {
  height: 80px;
}

header.sticky .logo {
  position: absolute;
  font-size: 2em;
  left: 100px;
  transform: translate(0, -50%);
}

header.sticky .fondo {
  opacity: 0.2;
}

header .fondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 1s;
}

@media (max-width: 992px) {
  header.sticky .fondo {
    opacity: 0;
  }
}
header .logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 9vw;
  color: #E10D2D;
  font-weight: 700;
  transition: 1s;
  text-decoration: none;
  z-index: 1;
}

@media (max-width: 992px) {
  header.sticky .logo {
    left: 40px;
  }
}
nav {
  position: relative;
  display: flex;
  z-index: 1;
}

nav ul {
  position: relative;
  display: flex;
  transition: 300ms;
  transform: translateX(100px);
  opacity: 0;
}

@media (max-width: 992px) {
  nav ul {
    display: none;
    opacity: 0;
    visibility: hidden;
  }
}
header.sticky nav ul {
  transform: translateX(0);
  opacity: 1;
  transition-delay: 500ms;
  visibility: visible;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  color: #fff;
  display: inline-block;
  padding: 10px 15px;
  font-size: 1.2em;
  text-decoration: none;
  transition-property: color;
  transition-duration: 300ms;
}

@media (max-width: 992px) {
  nav ul li a {
    font-size: 1.5em;
  }
}
nav ul li a:hover {
  color: #E10D2D;
}

section:first-of-type {
  margin-top: 50vh;
}

section {
  padding: 50px 100px;
}

@media (max-width: 992px) {
  section {
    padding: 40px;
  }
}
section h2 {
  font-size: 3rem;
}

@media (max-width: 992px) {
  section h2 {
    font-size: 2em;
  }
}
section p {
  font-size: 1.2em;
  margin-top: 20px;
}

@media (max-width: 992px) {
  header.sticky .toggle {
    position: fixed;
    top: 15px;
    right: 40px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    background: #fff url(../img/menusandwich.png);
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.3s;
  }

  header.sticky .toggle.active {
    background: #fff url(../img/close.png);
    background-size: 25px;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.3s;
  }

  nav.active ul {
    position: fixed;
    top: 80px;
    left: 0;
    width: 100%;
    height: calc(100% - 80px);
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .columnas-2 {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.contenedor-slides {
  position: absolute;
  width: 85%;
  height: 100vh;
  display: block;
  flex-direction: column;
  border: 2px solid #E10D2D;
  z-index: 1;
}

@media (min-width: 768px) {
  .contenedor-slides {
    display: flex;
    flex-direction: row;
  }
}
.contenedor-slides .slide {
  position: relative;
  background-color: #fff;
  height: 100%;
  flex: 1;
  border-left: 1px solid #E10D2D;
  overflow: hidden;
  transition: 0.5s ease-in-out;
}

.contenedor-slides .slide:last-child {
  border-right: none;
}

.contenedor-slides .slide:hover {
  flex-grow: 3;
}

.contenedor-slides .slide:nth-child(1) {
  background-image: url(../img/desarrollo.jpeg);
  background-position: center center;
  background-size: cover;
}

.contenedor-slides .slide:nth-child(2) {
  background-image: url(../img/redesss.jpeg);
  background-position: center center;
  background-size: cover;
}

.contenedor-slides .slide:nth-child(3) {
  background-image: url(../img/ecommerce.jpeg);
  background-position: center center;
  background-size: cover;
}

.contenedor-slides .slide .contenido {
  visibility: visible;
  position: absolute;
  bottom: 0;
  margin: 40px;
  padding: 30px;
  border-radius: 20px;
  background-color: #000;
  color: #E10D2D;
}
@media (min-width: 768px) {
  .contenedor-slides .slide .contenido {
    visibility: hidden;
    opacity: 0;
    transition: 0s;
    transform: translateY(100px);
  }
}

.contenedor-slides .slide .contenido p {
  color: #fff;
}

.contenedor-slides .slide:hover .contenido {
  visibility: visible;
  opacity: 1;
  transition: 0.5s;
  transition-delay: 0.5s;
  transform: translateY(0);
}

.contenedor-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  width: 554px;
  margin: 1rem;
  border-radius: 6px;
  box-shadow: 0.2px 4px 5px rgba(0, 0, 0, 0.3);
  background-color: #E10D2D;
  overflow: hidden;
  transition-property: transform;
  transition-delay: 0.1s;
  transition-duration: 0.5s;
}
@media (min-width: 768px) {
  .card {
    width: 362px;
  }
}

.card:hover .card-info {
  background-color: #000;
}

.card:hover {
  transform: scale(1.015) rotate(2deg);
}

.card-info {
  color: #eee;
  padding: 1rem 2rem;
  text-align: center;
  transition-property: background-color;
  transition-duration: 300ms;
}
.card-info h3 {
  font-size: 2.6rem;
}

.card-curso {
  width: 100%;
  transform: rotate3d(1, 2, -1, 40deg);
  box-shadow: 0.2px 4px 5px rgba(0, 0, 0, 0.7);
  margin-top: 5rem;
  z-index: -1;
}

.card-curso:hover {
  transform: scale(0.95) rotate3d(0, 0, 0, 0deg);
}

@media (min-width: 768px) {
  .grid-contacto {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}

.campo {
  margin: 1.5rem 0;
  padding: 1rem;
  border-radius: 6px;
  border: 2px solid #E10D2D;
}
.campo label, .campo option, .campo select, .campo input {
  font-size: 2rem;
  display: block;
  padding: 0.5rem;
}
.campo input, .campo select {
  width: 100%;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .campo input, .campo select {
    width: auto;
  }
}

.boton {
  padding: 1rem;
  border: 2px solid #000;
  background-color: #E10D2D;
  font-size: 1.5rem;
  text-transform: uppercase;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

.contenedor-redes {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(3, 1fr);
}

.red {
  align-content: center;
}

.red p {
  text-align: center;
  background-color: #E10D2D;
}